.loading {
  display: block;
  position: absolute;
  top: 50%;
  left: calc(50% - 25px);
  height: 50px;
  z-index: 199;
  width: 50px;
  /* margin: -25px 0 0 -25px; */
  border: 4px rgba(0, 0, 0, 0.25) solid;
  border-top: 4px black solid;
  border-bottom: 4px black solid;
  border-radius: 50%;
  animation: spin1 1s infinite linear;
}
@keyframes spin1 {
  from {
    transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
    transform: rotate(359deg);
  }
}

